import React from 'react';
import './Menu.scss';
import MenuItem from './MenuItem/MenuItem';

export default () => {
  return (
    <nav className="menu">    
        <ul>
            <MenuItem 
              contents="O NAS" 
              selector = ".page-info-card-scroll" 
              clickSelector = '.page-info'
            />
            <MenuItem 
              contents="OFERTA" 
              selector = ".page-info-card-scroll"
              clickSelector = '.offer'
            />
            <MenuItem 
              contents="SHOW" 
              selector = ".page-info-card-scroll"
              clickSelector = '.feel-the-reel'  
            />
            <MenuItem contents="FILMY" selector=".page-videos-scroll"/>
            <MenuItem contents="GALERIA" selector=".page-gallery-scroll"/>
            <MenuItem contents="KONTAKT" selector=".page-footer-scroll"/>
        </ul>        
    </nav>
  )
}
